"use client";
import type { Metadata } from "next";
import { ThemeProvider } from "@/components/theme/theme-provider";
import { Kodchasan } from "next/font/google";
import "../globals.css";
import Navbar from "@/components/header/header";
import Footer from "@/components/footer/footer"; // Assuming Footer component exists
import { usePathname } from "next/navigation";

const kodchasan = Kodchasan({
  subsets: ["latin"], // Specify the subsets you need
  weight: ["400", "700"], // Choose weights
  variable: "--font-kodchasan", // CSS variable name for Tailwind
});

export const metadata: Metadata = {
  title: "Flow Finesse | Login",
  description: "Split Money With Ease",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  const pathname = usePathname();

  // Define routes where Footer is not shown
  const noFooterRoutes = ["/login", "/signup"];
  const showFooter = !noFooterRoutes.includes(pathname);

  return (
    <html lang="en">
      <body
        className={`${kodchasan.className} ${kodchasan.variable} antialiased`}
      >
        <ThemeProvider
          attribute="class"
          defaultTheme="system"
          enableSystem
          disableTransitionOnChange
        >
          <Navbar />
          {children}
          {showFooter && <Footer />}
        </ThemeProvider>
      </body>
    </html>
  );
}
